body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-bar_offset {
  padding: 0 1rem;
}


.todo-from {
  margin-top: 2rem;
}

.todo-wrapper {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  background-color: #ddd;
  padding: 10px;
  color: black;
  font-weight: 600;
}

.todo-wrapper:nth-child(2n) {
  background-color: #aaa;
}

.todo_checkbox input:checked + .todo_text {
  text-decoration: line-through;
}

.todo-wrapper .todo_text {
  color: black;
}

.todo-wrapper .material-icons {
  cursor: pointer;
}

.about-page {
  margin: 0 20px;
}

.active_link {
  color: red;
}